import axios from "axios";
import React, { useEffect, useState } from "react";
import { BASE_URL } from "../../utils/constants";
import CardView from "./CardView";

const ViewCurriculum = () => {
  let user = JSON.parse(localStorage.getItem("user"));
  const [status, setStatus] = useState("pending");
  const [curriculums, setCurriculums] = useState([]);
  const [deleted, setDeleted] = useState(0);

  useEffect(() => {
    let params = { status };
    if (user.role !== "admin") params.id = user.employeeId;
    let url = `${BASE_URL}/viewAllCurriculum`;
    console.log({ params });
    axios
      .get(url, { params })
      .then((response) => setCurriculums(response.data.data));
  }, [status, deleted]);
  console.log({ curriculums });
  return (
    <>
      {/* card */}

      <div className="details">
        <div className="cardHeader flex-column ">
          <div class="cardBox">
            <div
              class="card"
              style={{
                backgroundColor: status === "pending" && "#40bd9f",
                color: status === "pending" && "white",
              }}
              onClick={() => setStatus("pending")}
            >
              <div>
                <div class="numbers">Present Curriculum</div>
              </div>

              <div class="iconBx">
                <ion-icon name="eye-outline"></ion-icon>
              </div>
            </div>

            <div
              class="card"
              style={{
                backgroundColor: status === "approved" && "#40bd9f",
                color: status === "approved" && "white",
              }}
              onClick={() => setStatus("approved")}
            >
              <div>
                <div class="numbers">Past Curriculum</div>
              </div>

              <div class="iconBx">
                <ion-icon name="eye-outline"></ion-icon>
              </div>
            </div>
          </div>
          <CardView data={curriculums} deleted={setDeleted} />
        </div>
      </div>
    </>
  );
};

export default ViewCurriculum;
