import axios from "axios";
import React, { useEffect, useState } from "react";
import { Search as SearchIcon } from "react-bootstrap-icons";
import { useNavigate } from "react-router-dom";
import { useSelect } from "react-select-search";
import { BASE_URL } from "../../utils/constants";

const Search = () => {
  const [searchKey, setSearchKey] = useState("");
  let user = JSON.parse(localStorage.getItem("user"));
  const navigate = useNavigate();

  const handleSearch =()=>{
      let url = `${BASE_URL}/viewAllCurriculum?searchKey=${searchKey}${
        user.role !== "admin" ? `&&id=${user.employeeId}` : ""
      }`;

      const params = {};
      axios.get(url, { params }).then((response) => {
        localStorage.setItem("curriculums", JSON.stringify(response.data.data));
        window.location.replace(
          `/${user.role === "admin" ? "adminpanel" : "facultypanel"}/home`
        );
      });
  }
  return (
    <div>
      <div className="search">
          <input
            type="text"
            placeholder="Search here"
            onChange={(e) => setSearchKey(e.target.value)}
            onKeyDown={(e)=>{if(e.code==='Enter')handleSearch()}}
          />
          <SearchIcon onClick={handleSearch} style={{cursor:'pointer'}} />
      </div>
    </div>
  );
};

export default Search;
