import axios from "axios";
import React from "react";
import { Card, ListGroup } from "react-bootstrap";
import { Link } from "react-router-dom";
import { BASE_URL } from "../../utils/constants";
import DownloadButton from "../download";

const CardView = ({ data = [], deleted }) => {
  const user = JSON.parse(localStorage.getItem("user"));
  const handleDelete = (id) => {
    axios
      .delete(`${BASE_URL}/deleteCurriculum/${id}`)
      .then(() => {
        alert("success fully deleted");
        deleted((prev) => prev + 1);
      })
      .catch(() => {
        alert("error in deletion");
      });
  };

  return (
    <div className="d-flex flex-wrap justify-content-around">
      {data?.map((item) => {
        const {
          ReqName,
          trainingArea,
          reqCategory,
          institution,
          duration,
          status,
          refFile,
          assignedFaculty,
          _id,
          download,
        } = item;
        return (
          <Link
            to={user.role !== "admin" && `/facultypanel/assignedreqform`}
            state={{ id: _id }}
            onClick={() => {
              localStorage.setItem("currentReq", JSON.stringify(item));
            }}
          >
            <Card
              className="m-2 p-2 mb-5"
              style={{
                width: 300,
                textAlign: "left",
                marginBottom: "20px important",
              }}
            >
              {/* {JSON.stringify(item)} */}

              <Card style={{ width: "18rem" }}>
                <Card.Header>{ReqName}</Card.Header>
                <ListGroup variant="flush">
                  <ListGroup.Item>{trainingArea}</ListGroup.Item>
                  <ListGroup.Item>{reqCategory}</ListGroup.Item>
                  <ListGroup.Item>{institution}</ListGroup.Item>
                  <ListGroup.Item>{duration}</ListGroup.Item>
                  <ListGroup.Item>
                    {refFile}
                    <DownloadButton src={download} fileName={refFile} />
                  </ListGroup.Item>
                </ListGroup>
                <ListGroup.Item> {assignedFaculty?.userName}</ListGroup.Item>
                <ListGroup.Item> {status}</ListGroup.Item>
                <Card.Text>Last updated 3 mins ago</Card.Text>
                {/* <Card.Body>
                  <Card.Link href="#">Card Link</Card.Link>
                  <Card.Link href="#">Another Link</Card.Link>
                </Card.Body> */}
              </Card>

              {/* <div class="card-deck">
                <div class="card">
                  <div class="card-body">
                    <h5 class="card-title">{ReqName}</h5>
                    <ul class="list-group list-group-flush">
                      <li class="list-group-item">{trainingArea}</li>
                      <li class="list-group-item">{reqCategory}</li>
                      <li class="list-group-item">{institution}</li>
                      <li class="list-group-item">{duration}</li>
                      <li class="list-group-item">
                        {refFile}
                        <DownloadButton src={download} fileName={refFile} />
                      </li>
                      <li class="list-group-item">
                        {assignedFaculty?.userName}
                      </li>
                      <li class="list-group-item">{status}</li>
                    </ul>
                    <p class="card-text">
                      <small class="text-muted">Last updated 3 mins ago</small>
                    </p>
                  </div>
                </div>
              </div> */}
              {user.role === "admin" && (
                <div className="tools d-flex justify-content-between">
                  <Link
                    className="m-1"
                    to="/adminPanel/Reqform"
                    state={{ id: item._id }}
                    onClick={() => {
                      localStorage.setItem("req_form", JSON.stringify(item));
                    }}
                  >
                    <div className="btn">EDIT</div>
                  </Link>
                  <div onClick={() => handleDelete(_id)} className="btn ">
                    DELETE
                  </div>
                </div>
              )}
            </Card>
          </Link>
        );
      })}
    </div>
  );
};

export default CardView;
