import React, { useEffect } from 'react'
import CardView from '../ViewCurriculum/CardView';

const PanelHome = () => {
    const user =  JSON.parse(localStorage.getItem("user"));
    let curriculums = JSON.parse(localStorage.getItem("curriculums"));

  return (
    <div>
        Welcome {user.userName} to your faculty Panel!
        <br /><br />
        <CardView data={curriculums} />
    </div>
  )
}

export default PanelHome